@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.intro {
  min-height: 2000px;
}
section {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(/ami/static/media/a1.87c374c1.jpg) no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

.banner {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}
.banner #bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fff url(/ami/static/media/heart.ad618256.svg) no-repeat;
  background-position: center;
  background-size: 30px;

  mix-blend-mode: screen;
}
.container {
  position: relative;
  margin-top: 200vh;
  background: #c28285;
  padding: 100px;
}
.intro-Header {
  position: relative;
  top: 100px;
  z-index: 1;
  text-align: center;
  font-size: 3em;
  width: 100%;
  color: #c28285;
}

.intro-title {
  color: cornsilk;
}
.intro-description {
  color: cornsilk;
}

@media (min-width: 360px) {
  .intro-Header {
    font-size: 3em;
  }
  .intro-title {
    font-size: 2em;
    text-align: center;
  }
  .container {
    padding: 50px;
    margin-top: 200vh;
  }
  .intro-description {
    font-size: 12px;
  }
}

@media (min-width: 992px) {
  .intro-Header {
    font-size: 4em;
  }
  .intro-title {
    font-size: 2.5em;
    text-align: center;
  }
  .container {
    padding: 50px;
    margin-top: 200vh;
  }
  .intro-description {
    font-size: 25px;
    font-weight: 200;
  }
}

